.product {
	margin-bottom: 20px;
	position: relative;
	display: flex;
	border-radius: 4px;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
	background-color: #ffffff;
}
.product__favourite {
	position: absolute;
	z-index: 3;
}

.product__image {
	display: flex;
	position: relative;
	cursor: pointer;
}

.product__image-more-photo {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 24px;
	line-height: 1.5;
	font-weight: 600;
	color: var(--white);
	background-color: var(--popup-overlay);
	border-radius: 4px 0px 0px 4px;
}

.product__image-more-photo--active {
	display: flex;
}

.product__image-navigation {
	display: flex;
	position: absolute;
	/* bottom: 5px; */
	width: 100%;
	height: 100%;
	padding: 0 3px;
}

.product__navigation-column {
	display: flex;
	margin: 0 3px;
	width: 100%;
	/* background-color: cyan; */
}

.product__navigation-column span {
	align-self: end;
	margin-bottom: 5px;
	width: 100%;
	height: 4px;
	/* background-color: red; */
	background-color: var(--disabled-bg);
	opacity: 0.4;
}

.product__navigation-column--active span {
	opacity: 1;
}
.product__navigation-column--fifth {
	z-index: 2;
}
